*{
  font-family:  Helvetica, Arial, sans-serif;
}

body {
  padding: 60px;
}

input {
  
  color: black;
  width: 100%;
  font-size: 1.2rem;
  
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
}


.card-grid p{
  padding: 5px;
  text-align:center;
}

.card img{
  max-height: 170px;
  
}